import { convertFromRaw, convertToRaw } from 'draft-js';
import axios from "axios";
import { url } from "../url";
import { decryptId } from "../utils"
export const getAllCategory = () => dispatch => {
    axios
        .get(
            `${url}GetAllCategory`)
        .then(res => {
            dispatch({
                type: "GET_ALL_CATEGORY",
                payload: res.data
            });
        })
        .catch(err => console.log(err));
}

export const getInstructorByCompanyId = () => dispatch => {
    axios
        .get(
            `${url}GetInstructorByCompanyId?CompanyId=${decryptId()}`)
        .then(res => {
            dispatch({
                type: "GET_INSTRUCTOR_BY_COMPANYID",
                payload: res.data
            });
        })
        .catch(err => console.log(err));
}
